<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`Account${dataLayout}`" :key="Date.now()" :parent="this"/>
  </div>
</template>

<script>
import AccountListLayout from '@/views/account/account-root/account-browse/AccountListLayout'
import AccountCardLayout from '@/views/account/account-root/account-browse/AccountCardLayout'
import AccountPivotLayout from '@/views/account/account-root/account-browse/AccountPivotLayout'
import AccountTableMixin from '@/mixins/account/AccountTableMixin'

export default {
  name: 'account-browse',
  mixins: [AccountTableMixin],
  components: {
    AccountListLayout,
    AccountCardLayout,
    AccountPivotLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  mounted: function () {
    this.loadAccountTableData()
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
