import AccountService from '@/services/account/AccountService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let AccountTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    lcyOnly: {
      type: Boolean,
      default: null
    },
    fcyOnly: {
      type: Boolean,
      default: null
    },
    ignoreAccountNumber: {
      type: String,
      default: null
    },
    typeId: {
      type: String,
      default: null
    },
    typeBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      accountTableData: [],
      accountTableContainer: 'loading-container',
      accountTableBusy: false,
      accountTableTotalRows: 0,
      accountTableCurrentPage: 1,
      accountTablePerPage: 10,
      columnDefs: [{
        headerName: this.$t('ACCOUNT_TITLE'),
        field: 'nickname'
      }, {
        headerName: this.$t('ACCOUNT_NUMBER'),
        field: 'accountNumber',
        format: 'Number'
      }, {
        headerName: this.$t('IBAN'),
        field: 'iban'
      }, {
        headerName: this.$t('ACCOUNT_TYPE'),
        field: 'typeName'
      }, {
        headerName: this.$t('CURRENCY'),
        field: 'currencyAbbreviation'
      }, {
        headerName: this.$t('AVAILABLE_BALANCE'),
        field: 'availableBalance',
        format: 'Decimal'
      }]
    }
  },
  computed: {
    accountTableNoDataText: function () {
      return this.accountTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    accountTableTotalPages: function () {
      return Math.ceil(this.accountTableTotalRows / this.accountTablePerPage)
    },
    accountTablePagingInfo: function () {
      return this.pagingInfo(this.accountTableCurrentPage, this.accountTablePerPage, this.accountTableTotalRows)
    }
  },
  methods: {
    loadAccountTableData (loadingContainerId) {
      let self = this
      this.accountTableBusy = true
      this.accountTableContainer = loadingContainerId || this.accountTableContainer
      this.startLoading(this.accountTableContainer)
      AccountService.accounts({
        lcyOnly: this.lcyOnly,
        fcyOnly: this.fcyOnly,
        ignoreAccountNumber: this.ignoreAccountNumber,
        typeId: this.typeId,
        typeBehaviourEnumId: this.typeBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.accountTableContainer)
        self.accountTableBusy = false
        self.accountTablePerPage = response.data.totalRows
        self.accountTableTotalRows = response.data.totalRows
        self.accountTableData = response.data.accountList || []
      }).catch(function (error) {
        self.stopLoading(self.accountTableContainer)
        self.accountTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshAccountTable () {
      this.loadAccountTableData()
    }
  },
  watch: {
    accountTableCurrentPage: function () {
      this.refreshAccountTable()
    },
    lcyOnly: function () {
      this.refreshAccountTable()
    },
    fcyOnly: function () {
      this.refreshAccountTable()
    },
    ignoreAccountNumber: function () {
      this.refreshAccountTable()
    }
  }
}

export default AccountTableMixin
